import React from 'react';
import { Movie } from "./Movie";


function Movies(props) {
    const { movies = [] } = props
    return (
        <div className="movies">
            { movies.length ?
                movies.map((movie) => (
                    < Movie key={movie.imdbID} {...movie} searchMovies={props.searchMovies} />
                )) :
                <h1>Invalid search</h1>
            }
        </div>

    )
}

export { Movies };
